import React from 'react'
import {Card,CardHeader,Avatar,CardMedia,CardContent,Typography,Box,IconButton} from "@mui/material"
import EditIcon from '@mui/icons-material/Edit';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import {useNavigate} from 'react-router-dom';
import axios from "axios";
import {useStyles} from "./utils.js";

const Blog = ({title,description,imageURL,userName,isUser,id}) => {
  const classes = useStyles();
  const navigate = useNavigate();
  const handleEdit = (e) => {
    navigate(`/myBlogs/${id}`);
  }
  console.log(title,isUser);
  const deleteRequest = async () => {
    console.log("Inside Delete Request");
    const res = await axios.delete(`http://localhost:5000/api/blog/${id}`).catch(err=>console.log(err));
    console.log(res);
    const data = await res.data;
    console.log(data);
    return data;
  }
  const handleDelete = () => {
    deleteRequest()
    .then(()=>navigate("/"))
    .then(()=>navigate("/blogs"))
    ;

  }
  return (
    <div><Card sx={{ width: "40%", margin:'auto', mt:2,padding:2,boxShadow:"5px 5px 10px #ccc",
    ":hover:":{
    boxShadow:"10px 10px 20px #ccc"}
    }}>
      {isUser && (<Box display="flex">
      <IconButton onClick={handleEdit} sx={{ marginLeft: "auto" }} ><EditIcon color="warning" /></IconButton>
      <IconButton onClick={handleDelete}><DeleteForeverIcon color="error" /></IconButton>
      </Box> )}
    <CardHeader
      avatar={
        <Avatar className={classes.font} sx={{ bgcolor: "red" }} aria-label="recipe">
          {userName ? userName.charAt(0): ""}
        </Avatar>
      }      
      title={title}
      subheader="September 14, 2016"
    />
    <CardMedia
      component="img"
      height="194"
      image={imageURL}
      alt="Paella dish"
    />
    <CardContent>
      <hr />
      <br />
      <Typography variant="body2" color="text.secondary">
        <b>{userName} </b> {": "} {description}
      </Typography>
    </CardContent>    
  </Card></div>
  )
}

export default Blog